    #t_3_1.titulo-segundo
      h2 3.1 Modos de transporte

    p Es el sistema o el mtodo que se utiliza para realizar el traslado de la mercanca. Puede ser martimo, areo, terrestre o ferroviario.

    ImagenInfograficaB.color-acento-contenido.mb-5
