      div(x='33.75%', y='39.6%', tooltip='Areo')
        .row.justify-content-around.align-items-center.align-self-center.fnd-b
          .col-md-8 
            h3 Areo
            P Es el medio ms gil y rpido para el desarrollo de operaciones logsticas en el comercio internacional.
            h5 Ventajas:
            ul.lista-ul
              li
                | Entrega de las mercancas justo en el momento que se necesitan.
              li
                | Tasa de siniestralidad muy baja.
              li
                | Idneo para trnsitos de mercanca de alto valor, documentos, muestras, piezas, mercancas urgentes, perecederos.
              li
                | Es una va muy utilizada para llegar a mercados poco accesibles.
              li
                | La logstica es menos compleja.

            .h5 Desventajas:
            ul.lista-ul
              li
                |
                | Restricciones tcnicas y de seguridad.
              li
                | Altamente contaminante.
              li
                | Precios elevados de flete.
              li
                | Inadecuado para transporte de mercancas a granel, peso o volumen.
              li
                | Limitaciones en cuanto a las dimensiones, tamao y peso.

          .col-md-4
            figure
              img(src='@/assets/tema3/img-4.svg')

      div(x='66.6%', y='38.1%', tooltip='Terrestre')
        .row.justify-content-around.align-items-center.align-self-center.fnd-b
          .col-md-8 
            h3 Terrestre
            p Es el modo ms utilizado para el transporte de mercanca y el ms asequible para todo tipo de empresas.
            h5 Ventajas:
            ul.lista-ul
              li
                | Ofrece entregas rpidas y confiables.
              li
                | Es ms flexible, ya que permite la entrega de la mercanca puerta a puerta.
              li
                | Verstil.
            h5 Desventajas:
            ul.lista-ul
              li
                | Guerra en tarifas y fletes.
              li
                | Inseguridad en las carreteras.
              li
                | Congestiones de trfico.
              li
                | Condiciones de las vas.
              li
                | Limitaciones en cuanto a las dimensiones y tamao, por restricciones en carretera.

          .col-md-4
            figure
              img(src='@/assets/tema3/img-5.svg')

      div(x='35%', y='69%', tooltip='Ferroviario')
        .row.justify-content-around.align-items-center.align-self-center.fnd-b
          .col-md-8 
            h3 Ferroviario
            p Es el transporte de mercanca sobre una va frrea.
            h5 Ventajas:
            ul.lista-ul
              li
                | Capacidad para transportar grandes cantidades.
              li
                | Recorrido de largas distancias.
              li
                | Menor contaminacin.
              li
