          .col-md-8 
            h3 Martimo
            p Es utilizado para el transporte de mercanca de grandes volmenes, para recorrer largas distancias y a unos costos bajos.
            h5 Ventajas:
            ul.lista-ul
              li
                | El volumen y la capacidad de almacenaje de los buques.
              li 
                | Los fletes martimos son muy competitivos y econmicos.
              li 
                | Las condiciones climatolgicas no impiden su flujo, gracias a la robustez, resistencia y fiabilidad de los buques, ya que estos pueden desenvolverse con gran facilidad en ambientes complejos o inestables.
              li 
                | Es un modo de transporte que permite el traslado de carga lquida o peligrosa.
              li 
