    #t_3_3.titulo-segundo
      h2 3.3 Fletes de transporte

    p.mb-5 Es el precio o los costos inherentes al transporte, es decir, lo que se debe pagar por el traslado de una mercanca, desde su lugar de origen hasta su lugar de destino, en el medio de transporte seleccionado, y este se calcula de acuerdo con el peso o volumen de la carga, encontrando:

    .row.justify-content-center
      .col-lg-10
        .row.justify-content-center
          .col-lg-10
            .p-3(style='background-color: #5b46a9; border-top-left-radius: 30px; border-top-right-radius: 30px')
              h4.mb-0.text-white.text-center Flete martimo

        .row.justify-content-center.p-4.pb-0(
          style='background-color: #f3f9ff'
        )
          p.mb-5 Es el costo que debe pagarse por el transporte de mercanca de un puerto a otro. Esta tarifa se establece de acuerdo con el peso, volumen de la carga o unidad (contenedor) de transporte utilizada. Se componen de:

          .row.mb-5
            TabsA.color-acento-contenido
              .tarjeta.color-primario.p-4(
                titulo='Tarifa bsica',
                style='background-color: #ffff; border-radius: 0'
              )
                p Es el pago que se realiza directamente al transportador, o por medio de un agente, por el transporte y llegada de las mercancas en ptimas condiciones a su destino. Estos valores dependen de factores como: el tipo de mercanca, los costos de operacin tanto del buque como los portuarios, la ruta seleccionada y la oferta y demanda de estos.

              .tarjeta.color-primario.p-4(
                titulo='Recargos',
                style='background-color: #ffff; border-radius: 0'
              )
                p Son aquellos costos adicionales que estn determinados por situaciones puntuales que pueden afectar los costos del transportador, ya que no los puede controlar directamente, y que finalmente se suman a la tarifa bsica. Dichos costos son:
                ul.lista-ul
                  li 
                    i.fas.fa-circle(style='font-size: 9px; color: #fd8d25')
                    p.mb-0 Fluctuaciones en el precio del combustible (BAF).
                  li 
                    i.fas.fa-circle(style='font-size: 9px; color: #fd8d25')
                    p.mb-0 Ajustes por tipo de cambio (CAF).
                  li 
                    i.fas.fa-circle(style='font-size: 9px; color: #fd8d25')
                    p.mb-0 Recargo por demoras congestin) en los puertos (CS).
                  li 
                    i.fas.fa-circle(style='font-size: 9px; color: #fd8d25')
                    p.mb-0 Seguridad en los puertos (CSF).
                  li 
                    i.fas.fa-circle(style='font-size: 9px; color: #fd8d25')
                    p.mb-0 Manejo portuario (THC).

              .tarjeta.color-primario.p-4(
                titulo='Descuentos',
                style='background-color: #ffff; border-radius: 0'
              )
                p Son los descuentos que se realizan teniendo en cuenta la fidelidad de los clientes, los volmenes transportados y su frecuencia, por operaciones especiales, entre otros, y que se calculan sobre la tarifa bsica.

          .row.justify-content-center.mb-5
            h3 Tipos de contratacin
            p.mb-5 De acuerdo con el servicio contratado, es posible encontrar:
            .col-lg-11
              LineaTiempoD.color-primario
                .row(numero='1', titulo='Servicio intermodal')
                  p.mb-0 Es el servicio que se utiliza al usar dos o ms medios de transporte diferentes entre dos puntos, y para los cuales se deben tener presentes los requerimientos efectuados por el contratante de la carga.

                .row(numero='2', titulo='Servicio puerta (<em>door</em>)')
                  p.mb-0 Es el servicio que se utiliza para entregar la mercanca en destino, teniendo en cuenta que el transportador es responsable de transportar la carga desde la bodega definida por el embarcador (origen) hasta la bodega designada por el consignatario (destino). Todos los costos relacionados corren por cuenta del transportista.

                .row(numero='3', titulo='Servicio puerto (<em>pier</em>)')
                  p.mb-4 La lnea naviera es responsable de transportar la carga desde el puerto de origen hasta el puerto de destino. Aqu, el usuario es el responsable de su costo, y se clasifican en:

                  ul.lista-ul.mb-0
                    li 
                      i.fas.fa-circle.cle
                      p.mb-0
                        b Servicio <em>house to house</em>: 
                        b-none  el exportador es responsable del transporte, desde el punto de origen hasta el punto de destino, incluidos los distintos pasos intermedios.
                    li 
                      i.fas.fa-circle.cle
                      p.mb-0
                        b  Servicio <em>house to pier</em>: 
                        b-none  el exportador es responsable del transporte desde el punto de partida hasta que la mercanca se halle a bordo del buque en el puerto de origen.
                    li 
                      i.fas.fa-circle.cle
                      p.mb-0
                        b Servicio <em>pier to house</em>: 
                        b-none el importador es responsable del transporte, desde que la mercanca se encuentra a bordo del buque en el puerto de destino, hasta el punto de destino final.

          .row.mb-5
            h3 Tipos de flete
            p.mb-5 Dependiendo de la operacin incluida dentro del proceso de transporte de la mercanca, es posible encontrar diferentes tipos de fletes o tarifas:

            TabsA.color-acento-contenido
              .tarjeta.fnd-2.p-4.tab-aaa(
                titulo='<em>Free In (F.I.)</em>',
                style='border-radius: 0'
              ) 
                h4 <em>Free In (F.I.)</em>
                p Incluye transporte, desestiba y descargue en puerto destino. El vendedor queda a cargo del cargue y estibe en puerto de origen.
              .tarjeta.fnd-2.p-4.tab-aaa(
                titulo='<em>Free Out (F.O.)</em>',
                style='border-radius: 0'
              )
                h4 <em>Free Out (F.O.)</em>
                p Incluye cargue en origen, estiba y transporte. El comprador queda a cargo de desestibe y descargue en puerto destino.
              .tarjeta.fnd-2.p-4.tab-aaa(
                titulo='<em>Free In and Out (F.I.O)</em>',
                style='border-radius: 0'
              )
                h4 <em>Free In and Out (F.I.O)</em>
                p No incluye cargue ni descargue, solamente incluye el transporte.
              .tarjeta.fnd-2.p-4.tab-aaa(
                titulo='<em>Free In, Out and Stowed (F.I.O.S.)</em>',
                style='border-radius: 0'
              )
                h4 <em>Free In, Out and Stowed (F.I.O.S.)</em>
                p No incluye cargue ni descargue ni trimado.
              .tarjeta.fnd-2.p-4.tab-aaa(
                titulo='<em>Free In, Out, Stowed and Trimmed (F.I.O.S.T.)</em>',
                style='border-radius: 0'
              )
                h4 <em>Free In, Out, Stowed and Trimmed (F.I.O.S.T.)</em>
                p No incluye el transporte
              .tarjeta.fnd-2.p-4.tab-aaa(
                titulo='<em>Free In/Liner Out (F.I.L.O.)</em>',
                style='border-radius: 0'
              )
                h4 <em>Free In / Liner Out (F.I.L.O.)</em>
                p Aqu es posible escoger entre el costo del cargue, o cargue y estibe o cargue, estibe y trincado.
              .tarjeta.fnd-2.p-4.tab-aaa(
                titulo='<em>Liner In/Free Out (L.I.F.O)</em>',
                style='border-radius: 0'
              )
                h4 <em>Liner In / Free Out (L.I.F.O)</em>
                p Aqu es posible escoger entre descargue, o descargue y desestibe, o descargue, desestibe y destrincado.

        .row.justify-content-center(
          style='background-color: #f3f9ff; margin-bottom: 60px'
        )
          .col-lg-11
            .row.justify-content-center
              .col-lg-12
                h3 Liquidacin
                p El flete consiste en una tarifa bsica y diferentes tipos de recargos que se aplican por trfico y para todas las mercancas movilizadas. As pues, la tarifa bsica es el costo del transporte de un producto determinado entre dos puntos o lugares especficos; y los recargos son los elementos que se suman a dicha tarifa con la finalidad de poder compensar las variaciones en los costos y/o condiciones de la operacin.

            .row.justify-content-center.fondo-i
              .col-lg-12.px-md-5
                .row
                  .col-lg-8.align-self-center.fnd-bl
                    p Para la liquidacin de los fletes martimos, se toma el factor que ms favorezca, ya sea por el peso (tonelada) o por el volumen (m3-p3) de cada embarque; y dicha relacin tambin es conocida como "factor de estiba", el cual vara segn la densidad y peso especfico de cada producto. Por esto, es importante que el exportador siempre conozca cul es la relacin de su mercanca, para conocer por qu factor se le realizar la liquidacin; y esta relacin se puede calcular as:
                    figure
                      img.img-c.img-190(src='@/assets/tema3/frml.svg') 

                  .col

    .row.justify-content-around.align-items-center
      .col-lg-10
        .row.mb-0 
          .row.justify-content-center
            .col-lg-10
              .p-3(style='background-color: #5b46a9; border-top-left-radius: 30px; border-top-right-radius: 30px')
                h4.mb-0.text-white.text-center Flete areo
          
          .row.fnd-1a
            .col-md-8.p-5.pb-0
              p Es el costo que debe pagarse por el transporte de mercanca de un aeropuerto a otro.
              ul.lista-ul
                li 
                  i.fas.fa-circle.cle
                  | <b>Tipos de fletes</b>
              ul.lista-ul
                li
                  | Dependiendo del peso y caractersticas de la carga, es posible encontrar diferentes tipos de fletes o tarifas:
            .col-md-4.align-self-start.a-izq 
              figure
                img(src='@/assets/tema3/img-45.svg')
          
          .row.justify-content-center.fnd-1
            .col-lg-9
              .row.mb-5
                .tarjeta-numerada.color-acento-contenido.p-5(style='border-color: #AFAFAF; border-radius: 0; border-right: 0; border-left: 0; border-bottom: 2px solid #AFAFAF')
                  .tarjeta-numerada__numero
                    .h1.text-white a
                  p.mb-4.ps-lg-4
                    b Por carga general (<em>General Cargo Rates</em>): 
                    b-none usando los vehculos adecuados.
                  ul.lista-ul.ps-lg-4
                    li 
                      i.fas.fa-circle.cle
                      p.mb-0 <b>Flete normal,</b> para cargas con peso mximo de 45 kilos
                    li 
                      i.fas.fa-circle.cle
                      p.mb-0 <b>Flete con descuento,</b> para cargas con peso superior a los 45 kilos. Existen escalas de descuento dependiendo del peso.
                  
                .tarjeta-numerada.color-acento-contenido.p-5(style='border-color: #AFAFAF; border-radius: 0; border-top: 0; border-right: 0; border-left: 0; border-bottom: 2px solid #AFAFAF')
                  .tarjeta-numerada__numero
                    .h1.text-white b
                  p.mb-0.ps-lg-4
                    b Por clase de mercanca (<em>Class Rates</em>): 
                    b-none aplica para algunos tipos de mercanca, como catlogos, revistas, oro, animales vivos, entre otros.
                  
                  
                  
                .tarjeta-numerada.color-acento-contenido.p-5(style='border-color: #AFAFAF; border-radius: 0; border-top: 0; border-right: 0; border-left: 0; border-bottom: 2px solid #AFAFAF')
                  .tarjeta-numerada__numero
                    .h1.text-white c
                  p.mb-0.ps-lg-4
                    b Por carga especfica (<em>Specific Commodity Rates</em>): 
                    b-none aplica cuando, regularmente, se realizan envos con un peso mnimo del mismo producto al mismo destino.
                  
                  
                .tarjeta-numerada.color-acento-contenido.p-5(style='border-color: #AFAFAF; border-radius: 0; border-top: 0; border-right: 0; border-left: 0; border-bottom: 2px solid #AFAFAF')
                  .tarjeta-numerada__numero
                    .h1.text-white d
                  p.mb-0.ps-lg-4
                    b Por unidad de transporte (<em>ULD Rates</em>): 
                    b-none se aplica una tarifa fija segn la unidad o medio utilizado.
                  
                
                
                

            h3 Liquidacin
            p.mb-5 Para la liquidacin del flete areo, se debe tener en cuenta la cantidad en dlares, u otra moneda, que se cobra por cada kilo de peso bruto o de volumen, a conveniencia de la aerolnea.

            .row.justify-content-around.align-items-center
              .row.fnd-gris.mb-5.justify-content-around.align-items-center
                .col-lg-8.align-self-center
                  p El exportador podr aproximar la cantidad de kilos a la escala de la tarifa inmediatamente superior si con esto ve que el flete total es ms bajo. Para calcular y conocer los kilos de volumen, se tiene la siguiente frmula:
                  figure
                    img.img-c.img-190(src='@/assets/tema3/forml2.svg') 

                .col-lg-4
                  figure
                    img.py-3.img-400(src='@/assets/tema3/img-46.svg') 
            
            p La tarifa depende del volumen del embarque, as pues, a mayor cantidad, menor tarifa. En este sentido, se encuentran escalas de 1 a 44kg, de 45 a 99Kg, ms de 100kg, ms de 300kg, ms de 500kg y ms de 1.000 kg.
            
            p Cada una de las aerolneas, a travs de la IATA, establece tarifas generales de carga para cada una de sus rutas, teniendo en cuenta el origen y el destino, esto es, el valor mnimo por contrato y las escalas y tarifas correspondientes.
            
            p.mb-5 De igual forma, el exportador debe pagar otros cargos adicionales como son:

            .row.justify-content-center.mb-5
              .col-10.col-lg-4.mb-5.mb-lg-0
                figure
                  img.mx-auto(src='@/assets/tema3/img-63.svg' style='width: 295px;') 
              .col-10.col-lg-4.mb-5.mb-lg-0
                figure
                  img.mx-auto(src='@/assets/tema3/img-64.svg' style='width: 295px;') 
                
              .col-10.col-lg-4.mb-5.mb-lg-0
                figure
                  img.mx-auto(src='@/assets/tema3/img-65.svg' style='width: 295px;') 

            h3 Modalidades de pago
            p.mb-4 Dependiendo del momento en el que se realizar el pago del flete, se pueden encontrar las siguientes modalidades:

            .row.mb-5
              .col-md-6.col-lg.mb-5.mb-lg-0
                .tarjeta-avatar.t-av
                  img.img-65(src='@/assets/tema3/1.svg', alt='AvatarTop')
                  
                  .tarjeta.color-primario--borde
                    .p-4.px-lg-2
                      p.text-center 
                        b Prepagado (<em>Prepaid</em>)
                      p.text-center Los fletes son cancelados en el momento del embarque de la mercanca.
                    
              .col-md-6.col-lg.mb-5.mb-lg-0
                .tarjeta-avatar.t-av
                  img.img-65(src='@/assets/tema3/2.svg', alt='AvatarTop')
                  
                  .tarjeta.color-acento-contenido--borde
                    .p-4.px-lg-2
                      p.text-center 
                        b Por cobrar (<em>Collect</em>)
                      p.text-center Los fletes se cancelan en destino por el comprador.
                
              .col-md-6.col-lg.mb-5.mb-lg-0
                .tarjeta-avatar.t-av
                  img.img-65(src='@/assets/tema3/3.svg', alt='AvatarTop')
                
                  .tarjeta.color-acento-botones--borde
                    .p-4.px-lg-2
                      p.text-center 
                        b De retorno (<em>Return Freight</em>)
                      p.text-center.mb-0 Flete que paga el exportador cuando la mercanca enviada es rechazada en el puerto de destino o cuando esta se entrega en un puerto distinto al pactado en el conocimiento de embarque.

              .col-md-6.col-lg.mb-5.mb-lg-0
                .tarjeta-avatar.t-av
                  img.img-65(src='@/assets/tema3/4.svg', alt='AvatarTop')
                  
                  .tarjeta.color-secundario--borde.fnd-2
                    .p-4.px-lg-2
                      p.text-center 
                        b Prorrateado (<em>Prorate Freight</em>)
                      p.text-center.mb-0 Se paga cuando no es posible entregar la mercanca en el puerto de destino y por tanto debe desviarse a otro puerto, por lo cual se genera un flete proporcional por el nuevo trayecto.

            p.mb-5.text-center Al momento de fijar o negociar el flete, es importante tener en cuenta algunos factores:
              
            .row.justify-content-center
              .col-lg-11
                .crd_02.mb-5
                  .crd_02__icon
                    figure
                      img(src='@/assets/tema3/img-47.svg') 
                  .crd_02__txt
                    h4 Ruta
                    p Existen tarifas especficas en cada ruta, relacionadas con caractersticas como puertos, tiempo de trnsito, entre otros.

                .crd_02.mb-5
                  .crd_02__icon
                    figure
                      img(src='@/assets/tema3/img-48.svg') 
                  .crd_02__txt
                    h4 Producto
                    p Este factor es importante, ya que los productos se encuentran clasificados por su nombre tcnico y sus caractersticas, con el fin de poder fijar con precisin la tarifa.

                .crd_02.mb-5
                  .crd_02__icon
                    figure
                      img(src='@/assets/tema3/img-49.svg') 
                  .crd_02__txt
                    h4 Valor comercial
                    p Para ciertos productos, las tarifas siguen una escala progresiva y ascendente, pues el valor de la mercanca por tonelada  flete define el costo final del mismo.

                .crd_02.mb-5
                  .crd_02__icon
                    figure
                      img(src='@/assets/tema3/img-50.svg') 
                  .crd_02__txt
                    h4 Relacin peso-volumen
                    p En este factor, las tarifas siguen una escala progresiva y ascendente, teniendo en cuenta el factor de estiba.

                .crd_02.mb-5
                  .crd_02__icon
                    figure
                      img(src='@/assets/tema3/img-51.svg') 
                  .crd_02__txt
                    h4 Tipo de embalaje
                    p Este factor determina el grado de complejidad de la mercanca para su manipulacin y transporte.

                .crd_02.mb-5
                  .crd_02__icon
                    figure
                      img(src='@/assets/tema3/img-52.svg') 
                  .crd_02__txt
                    h4 Unitarizacin de la carga
                    p Se cotizan las tarifas de acuerdo con la unidad de carga de que se trate, que por lo general son menores, debido a su fcil manejo.

                .crd_02.mb-5
                  .crd_02__icon
                    figure
                      img(src='@/assets/tema3/img-53.svg') 
                  .crd_02__txt
                    h4 Productos peligrosos
                    p Este factor para fijar el flete depende del grado de peligrosidad de los productos, y se ver reflejado en los recargos aplicados.

    separador
    #t_3_3.titulo-segundo
      h2 3.4 Consolidacin y desconsolidacin (FCL, LCL)

    p.mb-5 Permite lograr la optimizacin del espacio del contenedor, as como facilitar la manipulacin y circulacin de la mercanca dentro de este. Generalmente, es utilizada por las pequeas y medianas empresas, por el bajo volumen que manejan. Se consolida la carga, que consiste en la agrupacin de varias cargas de diferentes empresas, en una misma unidad de transporte, a menores costos; y la desconsolidacin, que consiste en desagrupar aquella carga consolidada.

    .row.justify-content-around.align-items-center
      .col-lg-10
        .row.mb-0 
          .row.mb-0.fnd-3a.pt-4
            h4.text-white.text-center.pt-6 A la hora de contratar el contenedor, se puede hacer de la siguiente forma:
            
          .row.justify-content-center.degra
            .col-lg-11
              .row.mt-4 
                .row.tarjeta--container
                  .col-md.tarjeta.p-4
                    .row.justify-content-center.mb-4
                      .col-8.col-lg-10
                        figure
                          img.mx.auto(src='@/assets/tema3/img-54.svg')
                        
                    h3.text-center FCL (<em>Full Container Load</em>)
                    p.mb-0 En este caso, el importador o el exportador hacen uso exclusivo de todo el contenedor, es decir, es llenado nicamente con su mercanca. En este caso, ya el transportador o el agente reciben el contenedor con la carga lista y embalada. Hay un nico dueo y una nica carga en el contenedor.
                        
                  .col-md.tarjeta.p-4
                    .row.justify-content-center.mb-4
                      .col-8.col-lg-10
                        figure
                          img.mx.auto(src='@/assets/tema3/img-55.svg', alt='Texto que describa la imagen')
                      
                    h3.text-center LCL (<em>Less Than Container Load</em>)
                    p A diferencia del trmino anterior, en este caso, el llenado del contenedor se realiza con mercancas de diferentes empresas, ya que no es posible hacer el llenado de este con una sola carga de una sola empresa; por tanto, aqu la responsabilidad corre por cuenta de la lnea naviera. En este caso, el transportador o el agente reciben las piezas de diferentes exportadores para ser consolidadas.
                    p.mb-0 Es esta una de las mejores opciones para aquellas empresas pequeas y medianas que manejan bajos volmenes de mercanca.


    separador
    #t_3_5.titulo-segundo
      h2 3.5 Riesgos y seguros
    p.mb-5 El seguro es la forma ms obvia y antigua de controlar el riesgo. A continuacin, se encuentran los principales riesgos de transporte y las plizas con mejor cobertura:

    .row.justify-content-center
      .col-lg-10
        .row.justify-content-center
